<script>
export default {
  data() {
    return {
      menuOpen: false,
      showProducts: false,
      showSolutions: false,
      showBackground: false,
      isMobile: false,
      mouseX: 0,
      mouseY: 0,
      animatedElements: [],
      throttledScroll: null,
      // 轮播相关变量
      dots: [],
      touchStartX: 0,
      touchEndX: 0,
      currentIndex: 0,
      // 新增鼠标拖动变量
      isDragging: false,
      startX: 0,
      scrollLeft: 0
    };
  },
  mounted() {
    this.initAnimations();

    this.checkScreenSize();
    window.addEventListener('resize', this.checkScreenSize);

    document.body.classList.add('page-loaded');
    document.addEventListener('mousemove', this.handleMouseMove);

    // 获取动画元素
    this.animatedElements = document.querySelectorAll('.scroll-animate');
    
    // 初始化检查
    this.checkScroll();
    setTimeout(() => this.checkScroll(), 300);

    // 保存节流后的滚动函数，便于移除监听
    this.throttledScroll = this.throttle(this.checkScroll, 100);
    window.addEventListener('scroll', this.throttledScroll);

    // 初始化轮播功能
    this.initCarousel();
    
    // 监听窗口大小变化，重新初始化
    window.addEventListener('resize', this.handleResize);

    // 初始化其他动画
    this.initAnimations();
  
    // 强制首屏元素动画
    setTimeout(() => {
      this.animatedElements.forEach(el => {
        if (this.isInViewport(el)) el.classList.add('in-view');
      });
    }, 500);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.checkScreenSize);
    document.removeEventListener('mousemove', this.handleMouseMove);
    window.removeEventListener('scroll', this.throttledScroll);
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
    // 折叠面板交互逻辑
    toggleAccordion(event) {
      const titleEl = event.currentTarget;
      const item = titleEl.closest('.accordion-item');
      const isActive = item.classList.contains('active');
      
      // 关闭所有已展开的项
      document.querySelectorAll('.accordion-item').forEach(el => {
        el.classList.remove('active');
      });
      
      // 如果当前项不是活跃状态，则激活它
      if (!isActive) {
        item.classList.add('active');
      }
    },

    // 动画初始化方法
    initAnimations() {
      // 为淡入动画元素添加初始状态
      const fadeElements = document.querySelectorAll('.animate-fade-in');
      fadeElements.forEach(el => {
        el.style.opacity = '0';
        el.style.transform = 'translateY(20px)';
        el.style.transition = 'opacity 0.8s ease, transform 0.8s ease';
      });
      
      // 触发淡入动画
      setTimeout(() => {
        fadeElements.forEach(el => {
          const delay = el.dataset.delay || 0;
          setTimeout(() => {
            el.style.opacity = '1';
            el.style.transform = 'translateY(0)';
          }, delay);
        });
      }, 100);
    },

    checkScreenSize() {
      this.isMobile = window.innerWidth < 768;
      if (!this.isMobile) {
        this.menuOpen = false;
      }
    },

    throttle(func, delay) {
      let lastTime = 0;
      return (...args) => {
        const now = Date.now();
        if (now - lastTime >= delay) {
          func.apply(this, args);
          lastTime = now;
        }
      };
    },
    
    isInViewport(el) {
      const rect = el.getBoundingClientRect();
      const viewHeight = window.innerHeight || document.documentElement.clientHeight;
      return rect.top < viewHeight * 0.85 && rect.bottom > 0;
    },

    checkScroll() {
      this.animatedElements.forEach(el => {
        const inView = this.isInViewport(el);
        const delay = el.dataset.delay || 0;
        if (inView && !el.classList.contains('in-view')) {
          el.style.transitionDelay = `${delay}ms`;
          el.classList.add('in-view');
        } else if (!inView && el.classList.contains('in-view')) {
          el.classList.remove('in-view');
        }
      });
      
      // 导航栏滚动效果
      const scrollY = window.scrollY;
      const header = document.querySelector('.wrap_head');
      if (scrollY > 50) {
        header.classList.add('scrolled');
      } else {
        header.classList.remove('scrolled');
      }
    },

    toggleMenu() {
      this.menuOpen = !this.menuOpen;
      this.showBackground = this.menuOpen;
    },
    
    showSubmenu(type) {
      if (type === 'products') {
        this.showProducts = true;
      } else if (type === 'solutions') {
        this.showSolutions = true;
      }
    },
    
    hideSubmenu(type) {
      if (type === 'products') {
        this.showProducts = false;
      } else if (type === 'solutions') {
        this.showSolutions = false;
      }
    },
    
    handleMouseMove(e) {
      this.mouseX = e.clientX;
      this.mouseY = e.clientY;
    },

    // 初始化轮播
    initCarousel() {
      // 清除现有滚动点和事件
      this.clearDots();
      this.removeCarouselEvents();
      
      // 获取DOM元素
      const carousel = this.$refs.carousel;
      const dotsContainer = this.$refs.carouselDots;
      const productItems = carousel.querySelectorAll('.product-item');
      
      if (!carousel || !dotsContainer || productItems.length === 0) return;
      
      // 生成滚动点
      productItems.forEach((_, index) => {
        const dot = document.createElement('div');
        dot.classList.add('dot');
        if (index === 0) dot.classList.add('active');
        
        // 点击滚动点切换到对应产品
        dot.addEventListener('click', () => {
          this.goToSlide(index);
        });
        
        dotsContainer.appendChild(dot);
      });
      
      // 保存滚动点引用
      this.dots = dotsContainer.querySelectorAll('.dot');
      
      // 添加滚动事件监听
      carousel.addEventListener('scroll', this.handleCarouselScroll.bind(this));
    },
    
    // 滚动到指定幻灯片
    goToSlide(index) {
      const carousel = this.$refs.carousel;
      const productItems = carousel.querySelectorAll('.product-item');
      
      if (!carousel || !productItems[index]) return;
      
      const itemWidth = productItems[0].offsetWidth + 25; // 包含间距
      const scrollPosition = itemWidth * index;
      
      carousel.scrollTo({
        left: scrollPosition,
        behavior: 'smooth'
      });
      
      this.updateActiveDot(index);
    },
    
    // 处理轮播滚动事件
    handleCarouselScroll() {
      const carousel = this.$refs.carousel;
      const productItems = carousel.querySelectorAll('.product-item');
      
      if (!carousel || productItems.length === 0) return;
      
      // 计算当前可见的产品索引
      const scrollPosition = carousel.scrollLeft;
      const itemWidth = productItems[0].offsetWidth + 25; // 包含间距
      const activeIndex = Math.round(scrollPosition / itemWidth);
      
      this.currentIndex = activeIndex;
      this.updateActiveDot(activeIndex);
    },
    
    // 更新激活的滚动点
    updateActiveDot(activeIndex) {
      this.dots.forEach((dot, index) => {
        if (index === activeIndex) {
          dot.classList.add('active');
        } else {
          dot.classList.remove('active');
        }
      });
    },
    
    // 清除现有滚动点
    clearDots() {
      const dotsContainer = this.$refs.carouselDots;
      if (dotsContainer) {
        dotsContainer.innerHTML = '';
      }
      this.dots = [];
    },
    
    // 移除轮播事件监听
    removeCarouselEvents() {
      const carousel = this.$refs.carousel;
      if (carousel) {
        carousel.removeEventListener('scroll', this.handleCarouselScroll);
      }
    },
    
    // 处理窗口大小变化
    handleResize() {
      // 延迟执行，避免频繁触发
      setTimeout(() => {
        this.initCarousel();
      }, 300);
    },
    
    // 滚动轮播（左右按钮控制）
    scrollCarousel(direction) {
      const carousel = this.$refs.carousel;
      const productItems = carousel.querySelectorAll('.product-item');
      
      if (!carousel || productItems.length === 0) return;
      
      const itemWidth = productItems[0].offsetWidth + 25; // 包含间距
      const newIndex = this.currentIndex + direction;
      
      // 限制索引范围
      if (newIndex >= 0 && newIndex < productItems.length) {
        this.goToSlide(newIndex);
      }
    },
    
    // 触摸开始
    handleTouchStart(e) {
      this.touchStartX = e.changedTouches[0].screenX;
      this.startX = e.changedTouches[0].screenX;
      this.scrollLeft = this.$refs.carousel.scrollLeft;
    },
    
    // 触摸移动
    handleTouchMove(e) {
      if (!this.startX) return;
      
      const x = e.changedTouches[0].screenX;
      const walk = (x - this.startX) * 1.5; // 滚动速度
      this.$refs.carousel.scrollLeft = this.scrollLeft - walk;
    },
    
    // 触摸结束（处理滑动）
    handleTouchEnd(e) {
      this.touchEndX = e.changedTouches[0].screenX;
      this.handleSwipeGesture();
      this.startX = null; // 重置
    },
    
    // 处理滑动手势
    handleSwipeGesture() {
      const threshold = 50; // 滑动阈值
      const difference = this.touchStartX - this.touchEndX;
      
      // 向右滑动
      if (difference > threshold) {
        this.scrollCarousel(1);
      }
      
      // 向左滑动
      if (difference < -threshold) {
        this.scrollCarousel(-1);
      }
    },
    
    // 鼠标按下（用于桌面端拖动）
    handleMouseDown(e) {
      this.isDragging = true;
      this.startX = e.pageX - this.$refs.carousel.offsetLeft;
      this.scrollLeft = this.$refs.carousel.scrollLeft;
      this.$refs.carousel.style.cursor = 'grabbing';
    },
    
    // 鼠标移动（用于桌面端拖动）
    handleMouseMove(e) {
      if (!this.isDragging) return;
      e.preventDefault();
      const x = e.pageX - this.$refs.carousel.offsetLeft;
      const walk = (x - this.startX) * 1.5; // 滚动速度
      this.$refs.carousel.scrollLeft = this.scrollLeft - walk;
    },
    
    // 鼠标释放（用于桌面端拖动）
    handleMouseUp() {
      this.isDragging = false;
      this.$refs.carousel.style.cursor = 'grab';
    }
  }
}
