/* 核心优势与应用场景 整体布局 */
/* 外层容器 */
.advantage-section {
  width: 90%; /* 占满父容器宽度，可根据需要调整 */
  max-width: 1200px; /* 限制最大宽度，避免过宽 */
  margin: 0 auto; /* 水平居中 */
  padding: 40px 20px; /* 上下内边距加大，让模块更舒展 */
}

/* 标题样式优化 */
.advantage-section h2 {
  text-align: center;
  font-size: 28px;
  font-weight: 700;
  color: #333;
  margin-bottom: 40px; /* 加大标题与内容间距 */
  position: relative;
  display: inline-block;
  left: 50%;
  transform: translateX(-50%); /* 确保标题绝对居中 */
}

/* 卡片容器网格布局 */
.advantage-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr)); 
  /* 自动适配列数，最小 280px，最大 1 等分 */
  gap: 30px; /* 加大卡片间距 */
  justify-items: center; /* 卡片水平居中 */
}

/* 单个卡片样式 */
.advantage-card {
  width: 100%; 
  max-width: 320px; /* 限制卡片最大宽度，让卡片更规整 */
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 6px 16px rgba(0,0,0,0.08); /* 更柔和的阴影 */
  padding: 30px; /* 加大内边距 */
  text-align: center;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center; /* 内容水平居中 */
}

/* 卡片悬停动画 */
.advantage-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 24px rgba(0,0,0,0.12); /* 悬停时增强阴影 */
}

/* 卡片顶部装饰条（如果有） */
.advantage-card::before {
  content: '';
  width: 60px;
  height: 6px;
  border-radius: 3px;
  margin-bottom: 15px;
}

