/* 移动端样式 */
@media screen and (max-width: 768px) {
    .bg-container {
        background-size: cover;
        background-position: center;
    }

    .wrap_head {
        height: 56px;
    }

    .head_on {
        height: 56px;
        padding: 0 15px;
    }

    .logo h1 {
        font-size: 20px;
    }

    .menu_list {
        display: flex;
        flex-direction: column;
        position: fixed;
        background: #fff;
        width: 100%;
        height: 100%;
        right: 0;
        top: 0;
        padding-top: 70px;
        z-index: 100;
        transform: translateY(-100%);
        opacity: 0;
    }

    .menu_list.active {
        transform: translateY(0);
        opacity: 1;
    }

    .menu_item {
        height: auto;
        margin-left: 0;
        padding: 15px 20px;
        border-bottom: 1px solid #f0f0f0;
    }

    .menu_item .txt {
        height: auto;
        line-height: 1.5;
    }

    .sub_list {
        position: relative;
        width: 100%;
        left: 0;
        top: 10px;
        transform: translateX(0);
        box-shadow: none;
        border-radius: 0;
        background-color: #f9f9f9;
    }

    .sub_list li {
        padding: 10px 0;
    }

    .menu-toggle {
        display: block;
    }

    .menu-toggle.open .bar:nth-child(1) {
        transform: translateY(8px) rotate(45deg);
    }

    .menu-toggle.open .bar:nth-child(2) {
        opacity: 0;
    }

    .menu-toggle.open .bar:nth-child(3) {
        transform: translateY(-8px) rotate(-45deg);
    }
}


