        <img src="https://images.unsplash.com/photo-1497366754035-f200968a6e72?ixlib=rb-4.0.3&auto=format&fit=crop&w=600&q=80">
        <img src="https://images.unsplash.com/photo-1552664730-d307ca884978?ixlib=rb-4.0.3&auto=format&fit=crop&w=600&q=80">
        <img src="https://images.unsplash.com/photo-1556761175-5973dc0f32e7?ixlib=rb-4.0.3&auto=format&fit=crop&w=600&q=80">
        <img src="https://images.unsplash.com/photo-1561070791-2526d30994b5?ixlib=rb-4.0.3&auto=format&fit=crop&w=600&q=80">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 添加滚动状态变量到组件数据中
      isScrolling: false,
      lastScrollTime: 0,
      touchStartY: 0,
      isHover: false,
      scrollTimer: null,
      currentPage: 1,       // 当前页码
      totalPages: 4         // 总页数，根据实际页面数量调整
    }
  },
  mounted() {
    this.initCollisionAnimation();
    this.initAnimations();
    this.initEventListeners();
    // 初始化当前页码
    this.updateCurrentPage();
  },
  beforeDestroy() {
    // 组件销毁前移除所有事件监听
    this.removeEventListeners();
  },
  methods: {
   // 初始化M碰撞动画
   initCollisionAnimation() {
      const loader = document.getElementById('loader');
      const mLeft = document.querySelector('.m-left');
      const mRight = document.querySelector('.m-right');
      const companyName = document.getElementById('company-name');
      const impactEffect = document.querySelector('.impact-effect');
      const logoContainer = document.querySelector('.logo-container');

      if (!loader || !mLeft || !mRight || !companyName || !impactEffect || !logoContainer) return;

      // 1. 初始位置设置
      mLeft.style.transform = 'translateX(-50px) translateY(-50%)';
      mRight.style.transform = 'translateX(50px) translateY(-50%)';
      // 确保logo初始状态完全隐藏
      logoContainer.style.transform = 'translate(-50%, -50%) scale(0.8)';
      logoContainer.style.opacity = '0';
      
      // 2. 移动并碰撞 - 稍慢的初始动画
      setTimeout(() => {
        mLeft.style.transition = 'transform 1.2s cubic-bezier(0.34, 1.56, 0.64, 1)';
        mRight.style.transition = 'transform 1.2s cubic-bezier(0.34, 1.56, 0.64, 1)';
        mLeft.style.transform = 'translateX(15px) translateY(-50%)';
        mRight.style.transform = 'translateX(-15px) translateY(-50%)';
        
        // 3. 碰撞瞬间效果 - 延长碰撞动画时间
        setTimeout(() => {
          // 碰撞时的挤压效果
          mLeft.style.transition = 'transform 0.3s ease-out, opacity 0.5s ease-out';
          mRight.style.transition = 'transform 0.3s ease-out, opacity 0.5s ease-out';
          mLeft.style.transform = 'translateX(20px) translateY(-50%) scale(1.1)';
          mRight.style.transform = 'translateX(-20px) translateY(-50%) scale(1.1)';
          
          // 碰撞波纹效果 - 更流畅的波纹
          impactEffect.style.transition = 'transform 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94), opacity 0.8s ease-out';
          impactEffect.style.transform = 'translate(-50%, -50%) scale(8)';
          impactEffect.style.opacity = '0';
          
          // 4. 碰撞后显示logo - 优化的时间安排和过渡效果
          setTimeout(() => {
            // M字母缓慢消失
            mLeft.style.transition = 'transform 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94), opacity 0.6s ease-out';
            mRight.style.transition = 'transform 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94), opacity 0.6s ease-out';
            mLeft.style.transform = 'translateX(40px) translateY(-50%) scale(0.5)';
            mRight.style.transform = 'translateX(-40px) translateY(-50%) scale(0.5)';
            mLeft.style.opacity = '0';
            mRight.style.opacity = '0';
            
            // 平滑显示logo - 使用更自然的ease-out曲线
            setTimeout(() => {
              logoContainer.style.transition = 'all 1.2s cubic-bezier(0.25, 0.46, 0.45, 0.94)';
              logoContainer.style.opacity = '1';
              logoContainer.style.transform = 'translate(-50%, -50%) scale(1)';
              
              // 显示公司名称 - 延迟出现，增加层次感
              setTimeout(() => {
                companyName.style.transition = 'opacity 1.2s ease-out, transform 1.2s ease-out';
                companyName.style.opacity = '1';
                companyName.style.transform = 'translate(-50%, -20px)';
                
                // 隐藏加载动画 - 足够的时间让用户看清logo
                setTimeout(() => {
                  loader.style.transition = 'opacity 1s ease-out';
                  loader.style.opacity = '0';
                  setTimeout(() => {
                    loader.style.display = 'none';
                  }, 1000);
                }, 1500); // 增加logo展示时间
              }, 500);
            }, 300); // M消失和logo出现之间的短暂延迟
          }, 300); // 碰撞后稍等再开始消失
        }, 1200); // 延长M移动到碰撞点的时间
      }, 500);
    },
    
    // 更新当前页码
    updateCurrentPage() {
      const activePageEl = document.querySelector('.page-active');
      if (activePageEl) {
        this.currentPage = parseInt(activePageEl.dataset.page);
      }
    },
    
    // 滚动到上一页
    scrollToPrevPage() {
      if (this.currentPage > 1) {
        this.scrollToSection(`page${this.currentPage - 1}`);
      }
    },
    
    // 滚动到下一页
    scrollToNextPage() {
      if (this.currentPage < this.totalPages) {
        this.scrollToSection(`page${this.currentPage + 1}`);
      }
    },
    
    // 初始化事件监听器
    initEventListeners() {
      this.wheelHandler = this.handleWheel.bind(this);
      this.touchStartHandler = this.handleTouchStart.bind(this);
      this.touchEndHandler = this.handleTouchEnd.bind(this);
      this.mouseMoveHandler = this.handleMouseMove.bind(this);
      this.scrollHandler = this.handleScroll.bind(this);
      
      window.addEventListener('wheel', this.wheelHandler);
      window.addEventListener('touchstart', this.touchStartHandler, { passive: true });
      window.addEventListener('touchend', this.touchEndHandler, { passive: true });
      window.addEventListener('mousemove', this.mouseMoveHandler);
      window.addEventListener('scroll', this.scrollHandler);
    },
    
    // 移除事件监听器
    removeEventListeners() {
      if (this.wheelHandler) window.removeEventListener('wheel', this.wheelHandler);
      if (this.touchStartHandler) window.removeEventListener('touchstart', this.touchStartHandler);
      if (this.touchEndHandler) window.removeEventListener('touchend', this.touchEndHandler);
      if (this.mouseMoveHandler) window.removeEventListener('mousemove', this.mouseMoveHandler);
      if (this.scrollHandler) window.removeEventListener('scroll', this.scrollHandler);
    },
    
    // 初始化动画元素
    initAnimations() {
      const animatedElements = document.querySelectorAll('.animate-fade-in');
      
      animatedElements.forEach(el => {
        el.style.opacity = '0';
        el.style.transform = 'translateY(20px)';
        const delay = parseInt(el.getAttribute('data-delay')) || 0;
        
        setTimeout(() => {
          el.style.transition = 'all 1000ms ease-out';
          el.style.opacity = '1';
          el.style.transform = 'translateY(0)';
        }, delay);
      });
    },
    
    // 滚轮事件处理
    handleWheel(e) {
      // 清除之前的计时器
      if (this.scrollTimer) {
        clearTimeout(this.scrollTimer);
      }
      
      // 阻止默认行为防止页面整体滚动
      e.preventDefault();
      
      const now = Date.now();
      const scrollCooldown = 800; 
      
      // 检查是否在冷却期内或正在滚动中
      if (this.isScrolling || now - this.lastScrollTime < scrollCooldown) {
        return;
      }
      
      this.scrollTimer = setTimeout(() => {
        this.isScrolling = true;
        this.lastScrollTime = now;
        
        const activePageEl = document.querySelector('.page-active');
        if (!activePageEl) {
          this.isScrolling = false;
          return;
        }
        
        const currentPage = parseInt(activePageEl.dataset.page);
        const totalPages = document.querySelectorAll('.fullpage').length;
        const scrollThreshold = 30;
        
        // 滚轮向上（前一页）
        if (e.deltaY < -scrollThreshold && currentPage > 1) {
          this.scrollToSection(`page${currentPage - 1}`);
        }
        // 滚轮向下（后一页）
        else if (e.deltaY > scrollThreshold && currentPage < totalPages) {
          this.scrollToSection(`page${currentPage + 1}`);
        } else {
          this.isScrolling = false;
          return;
        }
        
        // 冷却时间后重置滚动状态
        setTimeout(() => { 
          this.isScrolling = false; 
        }, scrollCooldown);
      }, 80);
      
      e.stopPropagation();
    },
    
    // 触摸开始事件
    handleTouchStart(e) {
      this.touchStartY = e.touches[0].clientY;
    },
    
    // 触摸结束事件
    handleTouchEnd(e) {
      const now = Date.now();
      const scrollCooldown = 800;
      
      if (!this.touchStartY || now - this.lastScrollTime < scrollCooldown) return;
      
      const activePageEl = document.querySelector('.page-active');
      if (!activePageEl) return;
      
      const touchEndY = e.changedTouches[0].clientY;
      const diff = this.touchStartY - touchEndY;
      const currentPage = parseInt(activePageEl.dataset.page);
      const totalPages = document.querySelectorAll('.fullpage').length;
      
      if (diff > 60 && currentPage < totalPages) {
        this.scrollToSection(`page${currentPage + 1}`);
        this.lastScrollTime = now;
      } else if (diff < -60 && currentPage > 1) {
        this.scrollToSection(`page${currentPage - 1}`);
        this.lastScrollTime = now;
      }
      
      this.touchStartY = 0;
    },
    
    // 鼠标移动事件（视差效果）
    handleMouseMove(e) {
      const x = (window.innerWidth - e.pageX) / 50;
      const y = (window.innerHeight - e.pageY) / 50;
      
      document.querySelectorAll('.parallax-element').forEach(el => {
        el.style.transform = `translate(${x}px, ${y}px) scale(1.05)`;
      });
    },
    
    // 滚动事件（导航栏样式）
    handleScroll() {
      const header = document.querySelector('.wrap_head');
      if (header) {
        if (window.scrollY > 50) {
          header.classList.add('scrolled');
        } else {
          header.classList.remove('scrolled');
        }
      }
    },
    
    // 平滑滚动到指定区域
    scrollToSection(sectionId) {
      const target = document.getElementById(sectionId);
      if (!target) {
        console.warn(`未找到ID为 ${sectionId} 的元素`);
        return;
      }

      document.querySelectorAll('.fullpage').forEach(page => {
        page.classList.remove('page-active', 'page-enter', 'page-exit');
        page.classList.add('page-exit');
      });

      target.classList.remove('page-exit');
      target.classList.add('page-active');
      
      // 更新当前页码
      this.updateCurrentPage();

      setTimeout(() => {
        this.initAnimations();
      }, 500);

      this.closeMobileMenu();
    },
    
    // 路由跳转前清理事件
    cleanupBeforeRoute() {
      this.removeEventListeners();
      this.closeMobileMenu();
    },
    
    // 移动端路由跳转处理
    handleMobileRoute() {
      this.removeEventListeners();
      this.closeMobileMenu();
    },
    
    // 移动端菜单切换
    toggleMobileMenu() {
      const mobileMenu = document.getElementById('mobileMenu');
      const menuToggle = document.getElementById('menuToggle');
      
      if (mobileMenu && menuToggle) {
        mobileMenu.classList.add('open');
        menuToggle.classList.add('active');
        document.body.style.overflow = 'hidden';
      }
    },
    
    // 关闭移动菜单
    closeMobileMenu() {
      const mobileMenu = document.getElementById('mobileMenu');
      const menuToggle = document.getElementById('menuToggle');
      
      if (mobileMenu && menuToggle) {
        mobileMenu.classList.remove('open');
        menuToggle.classList.remove('active');
        document.body.style.overflow = '';
      }
    },
    
    // 处理移动端导航点击
    handleMobileNav(sectionId) {
      this.scrollToSection(sectionId);
      this.closeMobileMenu();
    },
    
    // 图片卡片鼠标悬停效果
    hoverCard() {
      this.isHover = true;
    },
    
    // 图片卡片鼠标离开效果
    unhoverCard() {
      this.isHover = false;
    }
  }
};
</script>

<style scoped>
/* 加载动画样式 */
.loader {
  opacity: 1;
}

.m-left, .m-right {
  will-change: transform, opacity;
}

.impact-effect {
  will-change: transform, opacity;
}

.company-name {
  will-change: opacity, transform;
}

.logo-container {
  will-change: transform, opacity;
}
@keyframes loader-pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes ping {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
    transform: scale(1.2);
  }
}

.animate-spin {
  animation: spin 1s linear infinite;
}

.animate-ping {
  animation: ping 1.5s cubic-bezier(0, 0, 0.2, 1) infinite;
}

/* 引入外部字体 */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&family=Poppins:wght@400;500;600;700&display=swap');

/* 基础样式 */
body {
  font-family: 'Inter', system-ui, sans-serif;
  color: #1D2939;
  antialiased: true;
  overflow-x: hidden;
  background: linear-gradient(135deg, #f9fafb 0%, #f0f4f8 100%);
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Poppins', 'Inter', sans-serif;
}

/* 全屏页面容器 */
.page-container {
  position: relative;
  height: 100vh;
  overflow: hidden;
  perspective: 1200px;
}

/* 单个页面样式 */
.fullpage {
  position: absolute;
  inset: 0;
  transition: all 1200ms cubic-bezier(0.16, 1, 0.3, 1);
  transform-style: preserve-3d;
  backface-visibility: hidden;
}


.head_on {
  max-height: 50px;
}

/* 页面切换动画类 */
.page-enter {
  opacity: 0;
  transform: translateY(100px) scale(0.95) rotateX(10deg);
}
.page-active {
  opacity: 1;
  z-index: 10;
  transform: translateY(0) scale(1) rotateX(0);
}
.page-exit {
  opacity: 0;
  transform: translateY(-100px) scale(0.95) rotateX(-10deg);
}

/* 动画类 */
.animate-fade-in {
  opacity: 0;
  transition: all 1000ms ease-out;
}

.hover-lift {
  transition: transform 300ms;
}
.hover-lift:hover {
  transform: translateY(-4px);
}

.hover-scale {
  transition: transform 700ms ease-out;
}
.hover-scale:hover {
  transform: scale(1.05);
}

/* 导航菜单样式 */
.menu_item {
  position: relative;
  padding: 8px 16px;
  cursor: pointer;
}

.sub_list {
  position: absolute;
  left: 0;
  top: 100%;
  margin-top: 8px;
  width: 192px;
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  transition: all 300ms;
  transform: translateY(-8px);
}

.sub_list li {
  padding: 8px 16px;
  transition: background-color 300ms;
}
.sub_list li:hover {
  background-color: rgba(22, 93, 255, 0.05);
}

.sub_list li a {
  display: block;
  width: 100%;
  color: #4B5563;
  transition: color 300ms;
}
.sub_list li a:hover {
  color: #165DFF;
}

.menu_item:hover .sub_list {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

/* 移动端菜单 */
.mobile-menu {
  position: fixed;
  inset: 0;
  background-color: white;
  z-index: 50;
  transform: translateX(100%);
  transition: transform 500ms ease-in-out;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

.mobile-menu.open {
  transform: translateX(0);
}

.bar {
  display: block;
  width: 24px;
  height: 2px;
  background-color: #1D2939;
  margin: 6px 0;
  transition: all 300ms;
}

.menu-toggle.active .bar:nth-child(2) {
  opacity: 0;
}
.menu-toggle.active .bar:nth-child(1) {
  transform: rotate(45deg) translateY(8px);
}
.menu-toggle.active .bar:nth-child(3) {
  transform: rotate(-45deg) translateY(-8px);
}

/* 滚动时导航栏样式变化 */
.scroll-animate {
  transition: all 300ms;
}

.scrolled {
  background-color: white;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  padding-top: 8px;
  padding-bottom: 8px;
}

/* 玻璃拟态效果 */
.glass {
  background-color: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* 渐变按钮 */
.btn-gradient {
  background: linear-gradient(90deg, #165DFF 0%, #0E42D2 100%);
  color: white;
  transition: opacity 300ms;
}
.btn-gradient:hover {
  opacity: 0.9;
}

/* 优雅的边框 */
.elegant-border {
  position: relative;
}
.elegant-border::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 80px;
  height: 2px;
  background-color: #165DFF;
  transform: translateX(-50%) scaleX(0);
  transition: transform 500ms;
}
.elegant-border:hover::after {
  transform: translateX(-50%) scaleX(1);
}

/* 卡片样式 */
.card {
  background-color: rgba(255, 255, 255, 0.85);
  backdrop-filter: blur(12px);
  border-radius: 8px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
  overflow: hidden;
  transition: all 700ms;
}
.card:hover {
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.12);
  transform: translateY(-4px);
}

/* 3D卡片效果 */
.card-3d {
  transform: perspective(1000px) rotateX(0deg) rotateY(0deg);
  transform-style: preserve-3d;
  transition: transform 1000ms;
}
.card-3d:hover {
  transform: perspective(1000px) rotateX(0deg) rotateY(0deg) translateZ(20px);
}

/* 网格背景 */
.grid-bg {
  background-image: 
    linear-gradient(rgba(22, 93, 255, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(22, 93, 255, 0.03) 1px, transparent 1px);
  background-size: 40px 40px;
  background-position: center;
}

/* 动画定义 */
@keyframes pulse {
  0% { box-shadow: 0 0 0 0 rgba(22, 93, 255, 0.4); }
  70% { box-shadow: 0 0 0 10px rgba(22, 93, 255, 0); }
  100% { box-shadow: 0 0 0 0 rgba(22, 93, 255, 0); }
}

.parallax-element {
  transition: transform 0.5s ease-out;
}

/* 图片卡片样式 */
.image-card {
  cursor: pointer;
}

.image-overlay {
  transition: opacity 0.7s ease;
}

.image-card:hover .image-overlay {
  opacity: 1;
}

.image-card img {
  transition: all 1s ease;
}

.image-card:hover img {
  transform: scale(1.1);
  filter: blur(2px);
}

/* 页面导航按钮样式 */
.page-nav {
  /* 确保导航按钮在所有内容之上 */
  z-index: 30;
}

.page-nav-btn {
  cursor: pointer;
}

.page-dot {
  cursor: pointer;
  transition: all 0.3s ease;
}
</style>
