import { createRouter, createWebHashHistory } from 'vue-router'
import main from '../views/main.vue'
import wlw from '../views/wlw.vue'
import wlwsb from '../views/wlwsb.vue'
import jiejue from '../views/jiejue.vue'
import kjwl from '../views/kjwl.vue'
import about from '../views/about.vue'
import index from '../views/index.vue'

const routes = [
  {
    path: '/',
    name: 'index',
    component: index
  },

  {
    path: '/main',
    name: 'main',
    component: main
  },
  {
    path: '/wlw',
    name: 'wlw',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    //component: () => import(/* webpackChunkName: "about" */ '../views/yewu.vue')
    component: wlw
  },
  {
    path: '/wlwsb',
    name: 'wlwsb',
    component: wlwsb
  },
  {
    path: '/jiejue',
    name: 'jiejue',
    component: jiejue
  },
  {
    path: '/kjwl',
    name: 'kjwl',
    component: kjwl
  },
  {
    path: '/about',
    name: 'about',
    component: about
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})



// 添加全局导航守卫，实现路由切换时滚动到顶部
router.afterEach((to, from) => {

  window.scrollTo(0, 0)
})

export default router



