}

/* 响应式适配 */
@media (max-width: 768px) {
    .cover-content {
        padding: 180px 20px 0;
    }
    .cover-slogan {
        font-size: 32px;
    }
    .cover-desc {
        font-size: 16px;
        max-width: 90%;
    }
    .cover-btns {
        flex-direction: column;
        gap: 15px;
        padding: 0 20px;
    }
    .btn-primary, .btn-secondary {
        width: 100%;
        padding: 12px 0;
        font-size: 15px;
    }
}

/* 页面动画 */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(30px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
        transform: translateY(0);
    }
    to {
        opacity: 0;
        transform: translateY(30px);
    }
}

.scroll-animate {
    opacity: 0 !important;
    transform: translateY(30px) !important;
    transition: opacity 0.6s ease, transform 0.6s ease !important;
}
.scroll-animate.in-view {
    opacity: 1 !important;
    transform: translateY(0) !important;
}
            
/* 视差元素样式 */
.parallax {
    transition: transform 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

/* 内容容器 - 确保导航栏不被覆盖 */
.content-container {
    position: relative;
    z-index: 1;
    padding-top: 300px; /* 足够大的顶部间距，确保不被导航栏覆盖 */
}

.section-padding {
    padding-top: 10px;
    padding-bottom: 10px;
    align-items: center;
}

.section-title {
    font-size: 12px;
    font-weight: 600;
    margin-bottom: 10px;
    text-align: center;
}

.section-subtitle {
    font-size: 16px;
    font-weight: 400;
    margin-bottom: 20px;
    text-align: center;
    display: flex;
    flex-direction: column;
    margin-left: 250px;
}

/* 背景容器样式 - 固定顶部 */
.bg-cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 950px;
    background-image: url('../assets/主页配图7.png');
    background-position: top center;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1;
}

/* 导航栏样式 */
.wrap_head {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 80px;
    z-index: 99999; /* 确保在最上层 */
    background-color: rgba(0, 0, 0, 0.7);
    transition: background-color 0.3s ease, height 0.3s ease;
    box-shadow: 0 2px 20px rgba(0, 0, 0, 0.5);
}

.wrap_head.scrolled {
    background-color: rgba(0, 0, 0, 0.9);
    height: 70px;
}

/* 头部内容容器 */
.head_on {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 40px;
    position: relative;
}

/* Logo样式 */
.logo {
    display: flex;
    align-items: center;
    text-decoration: none;
    height: 100%;
}

.logo h1 {
    font-size: 24px;
    font-weight: 700;
    color: #fff;
    letter-spacing: 1px;
    transition: color 0.3s ease;
}

.logo h1:hover {
    color: #3498db;
}

/* 导航菜单 */
.menu_list {
    display: flex;
    height: 72px;
    transition: all 0.3s ease;
    position: relative;
    z-index: 2;
}

.menu_item {
    display: flex;
    align-items: center;
    height: 72px;
    margin-left: 40px;
    position: relative;
}

.menu_item .txt {
    display: block;
    height: 72px;
    line-height: 72px;
    font-size: 18px;
    font-weight: bold;
    color: #ffffff;
    text-decoration: none;
    transition: color 0.3s ease;
}

.menu_item .txt:hover {
    color: #0066cc;
}

/* 子菜单 */
.sub_list {
    display: none;
    position: absolute;
    width: 160px;
    left: 50%;
    top: 72px;
    transform: translateX(-50%);
    text-align: center;
    background-color: #fff;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    z-index: 300;
}

.sub_list li {
    padding: 8px 0;
}

.sub_list li a {
    display: block;
    color: #333;
    text-decoration: none;
    transition: color 0.3s ease;
}

.sub_list li a:hover {
    color: #0066cc;
}

/* 移动端菜单按钮 */
.menu-toggle {
    display: none;
    background: none;
    border: none;
    cursor: pointer;
    padding: 10px;
    z-index: 101;
}

.bar {
    display: block;
    width: 25px;
    height: 3px;
    background-color: #333;
    margin: 5px auto;
    transition: all 0.3s ease-in-out;
}

.bg-main {
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    z-index: -1000;
}

/* 跨境物联网优势 */
.position {
    width:75%;
    margin: 0 auto;
    padding: 40px 20px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin-top: 50px;
}
.Positionsection-header{
    margin-top: 70px;
}

/* 响应式三列布局 */
@media (min-width: 768px) {
    .position {
        flex-direction: row;
        align-items: flex-start;
    }
    
    .positionleft, .positionright, .position-center {
        width: calc(33.333% - 20px);
    }
}

/* 左侧框组样式 */
.positionleft, .positionright {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-top: -100px;
    gap: 10px;
}

/* 左侧/右侧上下框组公共样式 */
.positionleft-UP, .positionleft-Bottom, .positionright-UP, .positionright-Bottom {
    background-color: var(--bg-white);
    border-radius: 12px;
    padding: 24px;
    box-shadow: var(--shadow);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    border-left: 4px solid transparent;
}

/* 悬停效果 */
.positionleft-UP:hover, .positionleft-Bottom:hover, .positionright-UP:hover, .positionright-Bottom:hover {
    transform: translateY(-5px);
    box-shadow: var(--shadow-hover);
}

/* 内容介绍公共样式 */
.positionIntroduction {
    display: flex;
    flex-direction: column;
    gap: 6px;
}

/* 图标容器样式 */
.positionIntroduction::before {
    content: "";
    width: 48px;
    height: 48px;
    background-color: var(--primary)/10;
    border-radius: 8px;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color: var(--primary);
}

/* 左侧下框组图标样式（强调色） */
.positionleft-Bottom .positionIntroduction::before {
    background-color: var(--accent)/10;
    color: var(--accent);
}

/* 右侧上框组图标样式（辅助色） */
.positionright-UP .positionIntroduction::before {
    background-color: var(--secondary)/10;
    color: var(--secondary);
}

/* 标题样式 */
.positionIntroductionHeader {
    font-size: 18px;
    font-weight: 600;
    color: var(--text-dark);
    margin-bottom: 8px;
}

/* 文本样式 */
.positionIntroductionText {
    font-size: 14px;
    color: var(--text-medium);
    line-height: 1.6;
    margin-bottom: 12px;
}

/* 链接样式 */
.positionIntroduction-a {
    display: inline-flex;
    align-items: center;
    font-size: 14px;
    font-weight: 500;
    color: var(--primary);
    transition: color 0.3s ease;
}

.positionIntroduction-a:hover {
    color: var(--primary)/80;
}

.positionIntroduction-a i {
    margin-left: 4px;
}

/* 中间图片样式 */
.position-center {
    display: flex;
    justify-content: center;
    margin-top: 70px;
}

.position-center img {
    max-width: 100%;
    border-radius: 16px;
    transition: transform 0.5s ease;
}

.position-center img:hover {
    transform: scale(1.03);
}

/* 响应式调整（小屏幕优化） */
@media (max-width: 576px) {
    .position {
        padding: 20px 15px;
    }
    
    .positionleft-UP, .positionleft-Bottom,
    .positionright-UP, .positionright-Bottom {
        padding: 18px;
    }
    
    .positionIntroductionHeader {
        font-size: 16px;
    }
}

/* 通用模块标题样式 */
section {
  padding: 80px 20px;
}

.section-header {
  text-align: center;
  margin-bottom: 48px;
}

.section-title {
  font-size: clamp(1.75rem, 3vw, 2.25rem);
  font-weight: 700;
  color: var(--dark);
  margin-bottom: 12px;
}

.section-subtitle {
  font-size: 1rem;
  color: var(--gray);
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

/* 跨境物联网定位优势模块 */
.position-section {
  background-color: var(--light);
}

.position-grid {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr) 2fr repeat(2, 1fr); /* 5列布局：左右各2列小卡片，中间1列大图 */
  gap: 20px;
  align-items: center;
}

.position-card {
  background: var(--white);
  border-radius: var(--radius);
  padding: 24px;
  box-shadow: var(--shadow);
  transition: all 0.3s ease;
  text-align: center;
}

.position-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-hover);
}

.card-icon {
  width: 64px;
  height: 64px;
  margin: 0 auto 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--primary-light);
  border-radius: 50%;
  color: var(--primary);
  font-size: 24px;
}

.card-title {
  font-size: 18px;
  font-weight: 600;
  color: var(--dark);
  margin-bottom: 8px;
}

.card-desc {
  font-size: 14px;
  color: var(--gray);
  line-height: 1.6;
}

.position-center img {
  width: 100%;
  height: auto;
  border-radius: var(--radius);
  box-shadow: var(--shadow);
  transition: transform 0.5s ease;
}

.position-center img:hover {
  transform: scale(1.03);
}

/* 产品特点模块 */
.product-feature {
  background-color: var(--white);
}

.feature-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  align-items: center;
}

.feature-text {
  flex: 1;
  min-width: 300px;
}

.feature-desc {
  font-size: 16px;
  color: var(--gray);
  line-height: 1.7;
  margin-bottom: 24px;
}

.feature-list {
  list-style: none;
  margin-bottom: 32px;
}

.feature-list li {
  font-size: 16px;
  color: var(--dark);
  margin-bottom: 12px;
  line-height: 1.6;
  padding-left: 24px;
  position: relative;
}

.feature-list li i {
  position: absolute;
  left: 0;
  color: var(--primary);
  top: 3px;
}

.feature-image {
  flex: 1;
  min-width: 300px;
  border-radius: var(--radius);
  overflow: hidden;
  box-shadow: var(--shadow);
}

.feature-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* 技术参数模块 */
.tech-specs {
  background-color: var(--light);
}

.specs-table {
  max-width: 800px;
  margin: 0 auto;
  background-color: var(--white);
  border-radius: var(--radius);
  box-shadow: var(--shadow);
  overflow: hidden;
}

.specs-row {
  display: flex;
  border-bottom: 1px solid var(--light);
}

.specs-row:last-child {
  border-bottom: none;
}

.specs-category {
  flex: 0 0 30%;
  padding: 16px 20px;
  background-color: var(--primary-light);
  font-weight: 600;
  color: var(--primary-dark);
}

.specs-value {
  flex: 0 0 70%;
  padding: 16px 20px;
  color: var(--dark);
}

/* 应用场景模块 */
.application-scene {
  background-color: var(--white);
}

.scene-grid {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.scene-col {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.scene-row {
  display: flex;
  gap: 20px;
  flex: 1;
}

.scene-card {
  position: relative;
  border-radius: var(--radius);
  overflow: hidden;
  box-shadow: var(--shadow);
  transition: transform 0.3s ease;
}

.scene-card:hover {
  transform: translateY(-5px);
}

.scene-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}

.scene-card:hover img {
  transform: scale(1.05);
}

.scene-label {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 16px;
  background: linear-gradient(to top, rgba(0,0,0,0.8), transparent);
  color: var(--white);
}

.scene-label h3 {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 4px;
}

.scene-label p {
  font-size: 14px;
  opacity: 0.9;
}

.large {
  grid-row: span 2; /* 左侧大卡片占2行 */
  min-height: 400px;
}

.medium {
  flex: 1;
  min-height: 200px;
}

.small {
  flex: 1;
  min-height: 180px;
}

/* 平台功能模块 */
.platform-features {
  background-color: var(--light);
}

.platform-grid {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 24px;
}

.platform-card {
  background-color: var(--white);
  border-radius: var(--radius);
  padding: 30px;
  text-align: center;
  box-shadow: var(--shadow);
  transition: all 0.3s ease;
}

.platform-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-hover);
}

.platform-icon {
  width: 72px;
  height: 72px;
  margin: 0 auto 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--primary-light);
  border-radius: 50%;
  color: var(--primary);
  font-size: 32px;
}

.platform-title {
  font-size: 18px;
  font-weight: 600;
  color: var(--dark);
  margin-bottom: 12px;
}

.platform-desc {
  font-size: 14px;
  color: var(--gray);
  line-height: 1.6;
}

/* 技术支持模块 */
.tech-support {
  background-color: var(--white);
}

.support-grid {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 24px;
}

.support-card {
  background-color: var(--white);
  border-radius: var(--radius);
  padding: 30px;
  text-align: center;
  box-shadow: var(--shadow);
  transition: all 0.3s ease;
}

.support-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-hover);
}

.support-icon {
  width: 72px;
  height: 72px;
  margin: 0 auto 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--primary-light);
  border-radius: 50%;
  color: var(--primary);
  font-size: 32px;
}

.support-title {
  font-size: 18px;
  font-weight: 600;
  color: var(--dark);
  margin-bottom: 12px;
}

.support-desc {
  font-size: 14px;
  color: var(--gray);
  line-height: 1.6;
}

/* 客户案例模块 */
.customer-cases {
  background-color: var(--light);
}

.cases-slider {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  gap: 24px;
  overflow-x: auto;
  padding: 10px 0 30px;
  scroll-behavior: smooth;
}

.case-item {
  flex: 0 0 350px;
  background-color: var(--white);
  border-radius: var(--radius);
  padding: 24px;
  text-align: center;
  box-shadow: var(--shadow);
}

.case-item img {
  width: 100px;
  height: 100px;
  object-fit: contain;
  margin: 0 auto 20px;
  border-radius: 50%;
  background-color: var(--light);
  padding: 10px;
}

.case-item h3 {
  font-size: 18px;
  font-weight: 600;
  color: var(--dark);
  margin-bottom: 12px;
}

.case-item p {
  font-size: 14px;
  color: var(--gray);
  line-height: 1.6;
}

/* 联系咨询模块 */
.contact-section {
  background-color: var(--white);
  background-image: url('../assets/城市剪影.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}

.contact-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
}

.contact-form {
  flex: 1;
  min-width: 300px;
  background-color: var(--light);
  border-radius: var(--radius);
  padding: 30px;
  box-shadow: var(--shadow);
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: var(--dark);
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid #ddd;
  border-radius: var(--radius);
  font-size: 14px;
  transition: border-color 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary);
}

.contact-info {
  flex: 1;
  min-width: 300px;
}

.contact-info h3 {
  font-size: 20px;
  font-weight: 600;
  color: var(--dark);
  margin-bottom: 20px;
}

.contact-info p {
  font-size: 16px;
  color: var(--gray);
  margin-bottom: 16px;
  display: flex;
  align-items: center;
}

.contact-info p i {
  color: var(--primary);
  margin-right: 12px;
  font-size: 20px;
}

.qr-code {
  margin-top: 30px;
  text-align: center;
}

.qr-code img {
  width: 160px;
  height: 160px;
  margin-bottom: 12px;
  border: 1px solid var(--light);
  padding: 10px;
  border-radius: var(--radius);
}

.qr-code p {
  text-align: center;
}

/* 页脚设计 */
.footerBar {
    width:100%;
    min-height: 200px;
    background: #131a26;
    font-size: 16px;
    font-family: Noto Sans SC;
    font-weight: 400;
    margin-top: 20px; /* 改为正值，避免向上覆盖 */
    color: hsla(0, 0%, 100%, .5);
    width:99.2vw;
    margin-left: -10px;
}

.footerBar .function {
    width: 60%;
    margin: auto;
    display: flex;
    -webkit-box-pack: justify;
    justify-content: space-between;
    flex-wrap: wrap;
    border-bottom: 1px solid #d8d8d8;
}

.footerBar .functionStyle {
    width: 60%;
    margin: 64px auto auto;
    display: flex;
    -webkit-box-pack: justify;
    justify-content: space-between;
}

.footerBar .functionStyle .functionItem {
    width:36%;
}

.functionStyle .functionItem .footerText {
    white-space: nowrap;
    margin-top: 34px;
    line-height: 32px;
    font-size: 16px;
    font-weight: 400;
    color: hsla(0, 0%, 100%, .5);
}

.functionStyle .functionItem .functionHeader {
    font-size: 16px;
    font-weight: 500;
    color: #fff;
    line-height: 33px;
}

.functionStyle .functionItem .quicklinks {
    margin-top: 20px;
    display: flex;
    -webkit-box-pack: justify;
    justify-content: space-between;
    flex-wrap: wrap;
}

.footerBottom {
    width: 100%;
    padding-top: 64px;
    padding-bottom: 30px;
    text-align: center;
    background: #131a26;
    font-size: 14px;
    font-family: Noto Sans SC;
    font-weight: 400;
    color: hsla(0, 0%, 100%, .5);
    width:99.2vw;
    margin-left: -10px;
    margin-bottom: -10px;
}

/* 移动端样式 */
@media screen and (max-width: 768px) {
    .bg-container {
        background-size: cover;
        background-position: center;
    }

    .wrap_head {
        height: 56px;
    }

    .head_on {
        height: 56px;
        padding: 0 15px;
    }

    .logo h1 {
        font-size: 20px;
    }

    .menu_list {
        display: flex;
        flex-direction: column;
        position: fixed;
        background: #fff;
        width: 100%;
        height: 100%;
        right: 0;
        top: 0;
        padding-top: 70px;
        z-index: 100;
        transform: translateY(-100%);
        opacity: 0;
    }

    .menu_list.active {
        transform: translateY(0);
        opacity: 1;
    }

    .menu_item {
        height: auto;
        margin-left: 0;
        padding: 15px 20px;
        border-bottom: 1px solid #f0f0f0;
    }

    .menu_item .txt {
        height: auto;
        line-height: 1.5;
    }

    .sub_list {
        position: relative;
        width: 100%;
        left: 0;
        top: 10px;
        transform: translateX(0);
        box-shadow: none;
        border-radius: 0;
        background-color: #f9f9f9;
    }

    .sub_list li {
        padding: 10px 0;
    }

    .menu-toggle {
        display: block;
    }

    .menu-toggle.open .bar:nth-child(1) {
        transform: translateY(8px) rotate(45deg);
    }

    .menu-toggle.open .bar:nth-child(2) {
        opacity: 0;
    }

    .menu-toggle.open .bar:nth-child(3) {
        transform: translateY(-8px) rotate(-45deg);
    }
}

@media (max-width: 768px) {
  .cover-slogan {
    font-size: 2rem;
  }
  
  .menu_list {
    display: none; /* 移动端隐藏默认菜单 */
  }
  
  .menu-toggle {
    display: block;
  }
  
  .position-grid {
    grid-template-columns: 1fr; /* 手机：1列布局 */
  }
  
  .position-center {
    grid-column: span 1;
  }
  
  .scene-grid {
    grid-template-columns: 1fr;
  }
  
