  border-radius: 8px;
  color: #333;
}
.water-card .icon {
  width: 24px; 
  height: 24px; 
  background: url('data:image/svg+xml;utf8,<svg viewBox="0 0 24 24" fill="%234a8aff" xmlns="http://www.w3.org/2000/svg"><circle cx="12" cy="12" r="10" stroke="%234a8aff" stroke-width="2" fill="none" /></svg>') no-repeat center;
  background-size: contain;
}
.water-card p {
