    grid-column: 1 / 3; /* 中间卡片跨两列 */
  }
}
@media (max-width: 768px) {
  .grid-container {
    grid-template-columns: 1fr;
  }
  .main-card {
    grid-column: auto;
  }
}

/* FAQ 区域样式 */
