  color: #666;
  margin-bottom: 2rem;
}
.btn-support {
  display: inline-block;
  padding: 0.6rem 1.5rem;
  background: #2b62ff;
  color: #fff;
  border-radius: 25px;
  text-decoration: none;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}
.btn-support:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(43, 98, 255, 0.3);
}

/* 右侧 FAQ 内容区域 */
.faq-content {
  border-left: 1px solid #eee;
  padding-left: 4rem;
}
.accordion-item {
  border-bottom: 1px solid #eee;
  margin-bottom: 1rem;
}
.accordion-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1rem 0;
  font-size: 1.1rem;
  font-weight: 600;
  color: #333;
  background: transparent;
  border: none;
  cursor: pointer;
  transition: color 0.2s ease;
}
.accordion-title:hover {
  color: #2b62ff;
}
.accordion-icon {
  font-size: 1.2rem;
  font-weight: bold;
  color: #999;
  transition: transform 0.3s ease;
}
.accordion-content {
  max-height: 0;
  overflow: hidden;
  padding: 0 1rem; /* 移除底部内边距，确保完全隐藏 */
  line-height: 1.6;
  color: #666;
  transition: max-height 0.3s ease, padding 0.3s ease;
}
/* 展开状态 */
.accordion-item.active .accordion-content {
  max-height: 500px; /* 足够容纳内容的高度 */
  padding: 0 1rem 1rem; /* 展开时添加底部内边距 */
}
.accordion-item.active .accordion-icon {
  transform: rotate(45deg);
  color: #2b62ff;
}

/* FAQ 响应式适配 */
@media (max-width: 992px) {
  .faq-section {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  .faq-content {
    border-left: none;
    padding-left: 0;
    border-top: 1px solid #eee;
    padding-top: 2rem;
  }
}




