/* 斜切模块样式 */

.diagonal-section {
  position: relative;
  background-color: #2ebaef;
  overflow: hidden;
  /* 关键：让模块间距为0，避免空白 */
  margin-bottom: 0; 

  padding-bottom: 0;
}

.diagonal-section::before {
  content: '';
  position: absolute;
  top: -200px; 
  left: 0;
  width: 100%;
  height: 1000px; 
  background-color: #fff;
  /* 统一倾斜角度（如 -26deg） */
  transform: skewY(-26deg); 
  transform-origin: top left; 
  z-index: 1; 
}

.diagonal-section::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background-color: #fff;
  transform: skewY(-0deg);
  z-index: 1;
}





/* 斜块2：diagonal-section2 */
.diagonal-section2 {
  position: relative;
  background-color: #2ebaef;
  overflow: hidden;
  /* 关键：顶部间距为0，与斜块1衔接 */
  margin-top: 0; 
  padding-top: 0;
    margin-top: -100px;
}

.diagonal-section2::before {
  content: '';
  position: absolute;
  top: 0; 
  left: 0;
  width: 100%;
  /* 高度根据斜切角度调整，让斜边与斜块1衔接 */
  height: 400px; 
  background-color: #fff;
  /* 与斜块1对称倾斜（如 26deg，反向） */
  transform: skewY(-28.45deg); 
  transform-origin: top left; 
  z-index: 1; 
}

.diagonal-section2::after {
  content: '';
  position: absolute;
  bottom: 150;
  left: 0;
  width: 100%;
  height: 100px;
  background-color: #fff;
  transform: skewY(-0deg);
  z-index: 1;
}



.diagonal-section.inverse {
  background-color: var(--primary);
  color: #fff;
}

.diagonal-section.inverse::before,
.diagonal-section.inverse::after {
  background-color: #2ebaef;
}

.diagonal-content {
  display: flex;

  flex-wrap: wrap;
  align-items: center;
  position: relative;
  z-index: 2;



}

.diagonal-content.inverse {
  flex-direction: row-reverse;
}

.diagonal-text {
  flex: 1;
  min-width: 300px;
  padding: 20px;
}

